'use client';

import { useState, useEffect } from 'react';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';
import { collection, doc, writeBatch, query, orderBy, limit, onSnapshot, getDocs, getDoc, deleteDoc, updateDoc } from 'firebase/firestore';
import { db } from '@/lib/firebase';
import { AnimatePresence, motion } from 'framer-motion';
import {
    Zap, Users, Shield, Activity,
    Search, X, Check, History,
    LayoutGrid, Database, Link as LinkIcon, AlertCircle, Phone, FileText
} from 'lucide-react';

const getRankEmoji = (rank: string) => {
    const r = (rank || '').toLowerCase();
    if (r.includes('legend')) return 'üêâ';
    if (r.includes('grand')) return 'üëë';
    if (r.includes('diamond')) return 'üíé';
    if (r.includes('platinum')) return '‚ú®';
    if (r.includes('gold')) return 'ü•á';
    if (r.includes('silver')) return '‚öîÔ∏è';
    if (r.includes('bronze')) return 'üõ°Ô∏è';
    return 'üê£';
};

export default function AdminControlPanel() {
    const { user, loading } = useAuth();
    const router = useRouter();

    const [activeTab, setActiveTab] = useState<'OVERVIEW' | 'EXPLORER' | 'REQUESTS'>('OVERVIEW');

    // Data
    const [legacyUsers, setLegacyUsers] = useState<any[]>([]);
    const [users, setUsers] = useState<any[]>([]);
    const [requests, setRequests] = useState<any[]>([]);

    // Explorer
    const [explorerData, setExplorerData] = useState<any[]>([]);
    const [explorerSource, setExplorerSource] = useState<'rewards' | 'quests' | 'gym_standing' | null>(null);

    // Processing
    const [processingReqId, setProcessingReqId] = useState<string | null>(null);

    useEffect(() => {
        if (!loading && user) {
            const unsub1 = onSnapshot(collection(db, 'legacy_users'), (snap) => setLegacyUsers(snap.docs.map(d => ({ id: d.id, ...d.data() }))));
            const unsub2 = onSnapshot(collection(db, 'users'), (snap) => setUsers(snap.docs.map(d => ({ id: d.id, ...d.data() }))));
            const unsub3 = onSnapshot(collection(db, 'claim_requests'), (snap) => setRequests(snap.docs.map(d => ({ id: d.id, ...d.data() }))));
            return () => { unsub1(); unsub2(); unsub3(); };
        }
    }, [user, loading]);

    const loadSystemData = async (docName: 'rewards' | 'quests' | 'gym_standing') => {
        setExplorerSource(docName);
        setExplorerData([]);
        const docRef = doc(db, 'system_data', docName);
        const snap = await getDoc(docRef);
        if (snap.exists()) setExplorerData(snap.data().items || []);
        else alert('No data found for ' + docName);
    };

    const handleApproveClaim = async (req: any) => {
        if (!confirm(`Approve ${req.currentDisplayName}?`)) return;
        setProcessingReqId(req.id);
        try {
            const batch = writeBatch(db);
            const userRef = doc(db, 'users', req.uid);
            const requestRef = doc(db, 'claim_requests', req.id);
            const legacyRef = doc(db, 'legacy_users', req.requestedPhone);

            // Fetch latest legacy to ensure correctness
            const legacySnap = await getDoc(legacyRef);
            const legacyData = legacySnap.exists() ? legacySnap.data() : req.legacyData;

            batch.update(userRef, {
                rank: legacyData.rank,
                exp: legacyData.exp,
                party: legacyData.party || '',
                phone: req.requestedPhone,
                codename: legacyData.codename || '',
                rebirthCount: legacyData.rebirthCount || 0,
                expMultiplier: legacyData.expMultiplier || 1,
                isLegacyLinked: true,
                lastSynced: new Date().toISOString()
            });

            batch.delete(requestRef);
            batch.delete(legacyRef); // Reduce count in Waiting list

            await batch.commit();
        } catch (e: any) { alert(e.message); }
        finally { setProcessingReqId(null); }
    };

    const handleRejectClaim = async (reqId: string) => {
        if (!confirm('Reject?')) return;
        setProcessingReqId(reqId);
        try { await deleteDoc(doc(db, 'claim_requests', reqId)); }
        catch (e: any) { alert(e.message); }
        finally { setProcessingReqId(null); }
    };

    if (loading) return <div className="h-screen bg-[#1e1e24] flex items-center justify-center text-white">Loading...</div>;

    return (
        <div className="min-h-screen bg-[#1e1e24] text-slate-200 font-sans pb-24">

            {/* Header */}
            <div className="bg-[#2b2d42] p-4 shadow-lg sticky top-0 z-20 flex justify-between items-center border-b border-slate-700">
                <div className="flex items-center gap-2 font-bold text-white">
                    <Shield className="text-blue-500 fill-current" /> Admin Zone
                </div>
                <button onClick={() => router.push('/dashboard')} className="text-xs bg-slate-700 px-3 py-1 rounded hover:bg-slate-600 transition-colors">Client View</button>
            </div>

            {/* Tabs */}
            <div className="p-4 flex gap-2 overflow-x-auto">
                <button onClick={() => setActiveTab('OVERVIEW')} className={`px-4 py-2 rounded-xl text-sm font-bold ${activeTab === 'OVERVIEW' ? 'bg-blue-600 text-white' : 'bg-[#2b2d42]'}`}>Dashboard</button>
                <button onClick={() => setActiveTab('EXPLORER')} className={`px-4 py-2 rounded-xl text-sm font-bold ${activeTab === 'EXPLORER' ? 'bg-purple-600 text-white' : 'bg-[#2b2d42]'}`}>Explorer</button>
                <button onClick={() => setActiveTab('REQUESTS')} className={`px-4 py-2 rounded-xl text-sm font-bold relative ${activeTab === 'REQUESTS' ? 'bg-orange-600 text-white' : 'bg-[#2b2d42]'}`}>
                    Requests
                    {requests.length > 0 && <span className="absolute -top-1 -right-1 w-4 h-4 bg-red-500 rounded-full text-[10px] flex items-center justify-center">{requests.length}</span>}
                </button>
            </div>

            <div className="px-4">
                {/* OVERVIEW */}
                {activeTab === 'OVERVIEW' && (
                    <div className="space-y-4 animate-in fade-in">
                        <div className="grid grid-cols-2 gap-3">
                            <div className="bg-[#2b2d42] p-4 rounded-xl border border-slate-700">
                                <div className="text-[10px] font-bold text-slate-400">WAITING TO CLAIM</div>
                                <div className="text-2xl font-black text-white">{legacyUsers.length}</div>
                            </div>
                            <div className="bg-[#2b2d42] p-4 rounded-xl border border-slate-700">
                                <div className="text-[10px] font-bold text-green-400">CLAIMED (ACTIVE)</div>
                                <div className="text-2xl font-black text-white">{users.filter(u => u.phone).length}</div>
                            </div>
                        </div>

                        <div className="bg-[#2b2d42] rounded-xl overflow-hidden border border-slate-700">
                            <div className="p-3 bg-[#23232a] text-slate-400 text-xs font-bold uppercase">Member List (Waiting)</div>
                            <div className="max-h-[60vh] overflow-y-auto">
                                <table className="w-full text-left text-xs">
                                    <thead className="bg-[#2a2a35] text-slate-500 sticky top-0">
                                        <tr><th className="p-3">Rank</th><th className="p-3">Profile</th><th className="p-3 text-right">EXP</th></tr>
                                    </thead>
                                    <tbody className="divide-y divide-slate-700/30">
                                        {legacyUsers.map(u => (
                                            <tr key={u.phone} className="hover:bg-white/5">
                                                <td className="p-3 text-lg text-center w-12">{getRankEmoji(u.rank)}</td>
                                                <td className="p-3">
                                                    <div className="font-bold text-white">{u.displayName}</div>
                                                    <div className="text-[10px] text-slate-500">{u.codename} ‚Ä¢ {u.phone}</div>
                                                </td>
                                                <td className="p-3 text-right font-mono text-yellow-500">{u.exp}</td>
                                            </tr>
                                        ))}
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                )}

                {/* EXPLORER */}
                {activeTab === 'EXPLORER' && (
                    <div className="space-y-4 animate-in fade-in">
                        <div className="flex gap-2">
                            {['gym_standing', 'rewards', 'quests'].map(k => (
                                <button key={k} onClick={() => loadSystemData(k as any)} className={`flex-1 p-3 rounded-xl border text-xs font-bold ${explorerSource === k ? 'bg-purple-600/20 border-purple-500 text-purple-300' : 'bg-[#2b2d42] border-transparent text-slate-400'}`}>
                                    {k.replace('_', ' ').toUpperCase()}
                                </button>
                            ))}
                        </div>
                        <div className="bg-[#2b2d42] rounded-xl overflow-hidden min-h-[50vh] border border-slate-700/50">
                            {explorerData.length > 0 ? (
                                <div className="max-h-[60vh] overflow-auto">
                                    <table className="w-full text-xs whitespace-nowrap">
                                        <thead className="bg-[#23232a] text-slate-400 sticky top-0">
                                            <tr>{Object.keys(explorerData[0]).slice(0, 5).map(k => <th key={k} className="p-3 text-left">{k}</th>)}</tr>
                                        </thead>
                                        <tbody className="divide-y divide-slate-700/30">
                                            {explorerData.map((row, i) => (
                                                <tr key={i} className="hover:bg-white/5">
                                                    {Object.values(row).slice(0, 5).map((v: any, j) => <td key={j} className="p-3 border-r border-slate-700/30 text-slate-300">{String(v)}</td>)}
                                                </tr>
                                            ))}
                                        </tbody>
                                    </table>
                                </div>
                            ) : <div className="flex items-center justify-center h-full text-slate-500 text-xs py-20">Select a source above</div>}
                        </div>
                    </div>
                )}

                {/* REQUESTS */}
                {activeTab === 'REQUESTS' && (
                    <div className="space-y-3 animate-in fade-in">
                        {requests.length === 0 ? <div className="text-center text-slate-500 py-10">No pending requests</div> :
                            requests.map(req => (
                                <div key={req.id} className="bg-[#2b2d42] p-4 rounded-xl border border-slate-700">
                                    <div className="flex items-center gap-3 mb-3">
                                        <div className="w-10 h-10 rounded-full bg-slate-700 overflow-hidden">
                                            {req.profileImage && <img src={req.profileImage} className="w-full h-full" />}
                                        </div>
                                        <div>
                                            <div className="font-bold text-white">{req.currentDisplayName}</div>
                                            <div className="text-xs text-slate-400">{req.email}</div>
                                        </div>
                                        <div className="ml-auto text-[10px] bg-yellow-500/20 text-yellow-500 px-2 py-1 rounded">PENDING</div>
                                    </div>
                                    <div className="bg-[#23232a] p-2 rounded mb-3">
                                        <div className="text-[10px] text-slate-500">REQUESTING PHONE</div>
                                        <div className="text-lg font-mono font-bold text-white">{req.requestedPhone}</div>
                                    </div>
                                    <div className="flex gap-2">
                                        <button onClick={() => handleRejectClaim(req.id)} disabled={!!processingReqId} className="flex-1 py-2 bg-red-500/10 text-red-400 rounded-lg hover:bg-red-500/20 disabled:opacity-50">Reject</button>
                                        <button onClick={() => handleApproveClaim(req)} disabled={!!processingReqId} className="flex-1 py-2 bg-green-600 text-white rounded-lg hover:bg-green-500 disabled:opacity-50 shadow-lg shadow-green-900/20">Approve</button>
                                    </div>
                                </div>
                            ))}
                    </div>
                )}
            </div>
        </div>
    );
}
